/*EMPLOYEE PROFILE*/
.employee-profile {
  display: flex;
  align-items: center;
}
.employee-profile input {
  background: transparent;
  box-shadow: none;
  /* border-bottom: 1px solid white; */
  border-radius: 0px;
  text-align: center;
  color: white;
  font-size: 24px;
}
.employee-img {
  flex: 1;
}
.employee-info {
  flex: 2;
  color: white;
  font-weight: bold;
  font-size: 16px;
}
.field {
  display: flex;
  margin-bottom: 30px;
}
.employee-name,
.employee-birthday,
.employee-email,
.employee-phone,
.employee-completed,
.employee-progress {
  flex: 1;
}

.back-btn {
  text-align: right;
}
.back-btn img {
  width: 100px;
}

/*SIDEBAR*/

.sidebar-container {
  display: flex;
  min-height: 100vh;
}
.frosted_2 {
  /* Frosted glass affect */
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  /* Add box-shadow for more depth */
  box-shadow: 0px 10px 15px 10px rgba(0, 0, 0, 0.15);
  background-color: rgba(228, 228, 228, 0.1);
}
main {
  width: 100%;
  padding: 20px;
}
.sidebar {
  /* background: #000; */
  color: #fff;
  transition: all 0.5s;
}
.top-section {
  display: flex;
  align-items: center;
  padding: 20px 15px;
}
.logo {
  font-size: 25px;
}
.bars {
  display: flex;
  font-size: 25px;
  margin-left: 30px;
  cursor: pointer;
}
.link {
  display: flex;
  color: #fff;
  padding: 10px 15px;
  gap: 15px;
  transition: all 0.5s;
}
.link:hover {
  background: #55608f;
  color: #fff;
  transition: all 0.5s;
}
.active {
  background: lightcoral;
  color: #000;
}
.icon,
.link-text {
  font-size: 20px;
}

/*DASHBOARD CARDS*/
.cards-list {
  z-index: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  margin: 30px auto;
  width: 300px;
  height: 300px;
  border-radius: 40px;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 7px rgba(0, 0, 0, 0.22);
  cursor: pointer;
  transition: 0.4s;
}

.card .card_image {
  width: inherit;
  height: inherit;
  border-radius: 40px;
}

.card .card_image img {
  width: inherit;
  height: inherit;
  border-radius: 40px;
  object-fit: cover;
}

.card .card_title {
  text-align: center;
  border-radius: 0px 0px 40px 40px;
  font-family: poppins;
  font-weight: bold;
  font-size: 30px;
  margin-top: -80px;
  height: 40px;
}

.card:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 5px 5px 30px 15px rgba(0, 0, 0, 0.25),
    -5px -5px 30px 15px rgba(0, 0, 0, 0.22);
}

.title-white {
  color: white;
}

.title-black {
  color: black;
}

@media all and (max-width: 500px) {
  .card-list {
    /* On small screens, we are no longer using row direction but column */
    flex-direction: column;
  }
}

/*LOADING*/
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  4.9% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }
  5% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

/*mobile*/

@media only screen and (max-width: 991px) {
  .employee-profile {
    flex-direction: column;
  }
  .employee-profile img {
    width: 250px;
  }
}
